	module.exports = "<h1 id=->快速入门</h1> <p>通过一下几个简单的命令就可以开始一个项目的开发：</p> <pre><code class=\"hljs shell\"><span class=hljs-meta>#</span><span class=bash> 没有安装过命令行工具的，先进行安装</span>\nnpm i -g gem-mine\n<span class=hljs-meta>\n#</span><span class=bash> 快速初始化项目</span>\ngem-mine project_name\n<span class=hljs-meta>\n#</span><span class=bash> 启动项目</span>\ncd project_name\nnpm start\n</code></pre> <p>如果已经安装过 gem-mine 则无须重复安装，project_name 是你要创建的项目名称。</p> <p>在创建过程中，会有交互式的命令行提示，逐步提示你进行选择操作：</p> <ul> <li>选择项目运行的平台类型（PC 还是 移动端）</li> <li>是否对 IE8 进行支持</li> <li>是否使用 UI 组件库（提供了 PC 端：fish、ant design；移动端：fish-mobile、ant design mobile）</li> <li>以及是否使用一些常用的代码结构（目前提供了管理后台代码骨架）。</li> </ul> <p>下面是一个演示视频（清晰度压缩，时间不压缩。为了多展示功能（手动斜眼），选择了有管理后台的选项）</p> <video src=http://dn-tomjoke.qbox.me/6.mp4 controls preload> <div class=not-support>您的浏览器不支持视频播放，<a href=http://dn-tomjoke.qbox.me/6.mp4>请点击下载视频观看</a></div> </video> <p>无论你选择那一项，默认创建的项目中带会带有默认的 gem-mine 例子以供学习。</p> <p>是不是想马上来一发，来吧，少年！扫描二维码，入群讨论（标注 gem-mine）。</p> <p><img src=" + __webpack_require__(139) + " class=\"center thumb\"/></p> ";

