	module.exports = "<h1 id=->开发实例</h1> <p>下面通过 内置用例 和 开发项目实例 来大致介绍下 gem-mine 的业务开发过程</p> <h2 id=gem-mine->gem-mine 内置开发样例</h2> <p>使用 gem-mine 初始化项目后，通过 <code>/examples</code> 可以访问到提供的开发样例列表，<a href=#/examples>点击访问在线样例</a></p> <p>样例的代码主要位于 <code>src/components/examples</code> 目录下，样例包括以下功能：</p> <ul> <li>不同环境下的常量示例：该例子展示了不同环境下（依赖 npm 环境变量 env），例如本地环境、测试环境、生产环境中，一些常量需要不同的值，可以在此例子中得到帮助。本地要模拟不同 env 的值，可以通过指定参数 --env=xxx，例如 <code>npm start --env=production</code>，更多环境变量与常量的使用，<a href=#/docs/api/constant>参看文档：环境变量与常量</a></li> <li>计数器：该例子展示了 model 中 action、reducer、smart 的使用，更多 model 及其概念、使用 的帮助，<a href=#/docs/api/model>参看文档：model</a></li> <li>路由：该例子展示了 路由的配置（配置目录 <code>src/global/routes</code>，例子中给出了如何拆分路由的方案）、路由参数的获取（路径参数与查询参数）、构造路由路径、返回上一页的功能。更多 路由配置和使用的帮助，<a href=#/docs/api/router>参看文档：路由</a></li> <li>网络请求：该例子展示了 网络请求的配置（配置文件 <code>config/proxy.js</code> 以及 通用切面配置 <code>src/global/request.js</code>），使用 CORS 模式给出了 GET/POST 请求示例，包含了通用 loading、异常处理。更多 网络请求的配置和使用，参看 <a href=#/docs/api/request>参看文档：网络请求</a></li> <li>权限拦截：该例子展示了如何在路由层面判断是否有权限访问某个组件，此能力由路由配置提供</li> <li>404 页面：该例子展示了如何配置 404 页面（包含了子路由 404 页面），此能力也是由路由配置提供</li> <li>UI 组件例子：该例子展示了如何使用接入的 UI 组件库，如果没有使用，则无功能。不同 UI 组件库的用例可能会显示不一样，具体 UI 组件库的使用，参看对应文档，脚手架默认提供了 ant design、fish 相关的组件库作为最佳实践，当然你可以接入任意的 React UI 组件库，只是配置上需要你自己多费点心思。</li> </ul> <h2 id=-gem-mine->使用 gem-mine 开发一个项目</h2> <p><span class=red>你现在正在看的文档系统正是使用 gem-mine 创建的一个项目</span>。项目的源码地址：<a href=http://github.com/gem-mine/gem-mine-docs target=_blank>gem-mine-docs</a></p> <p>下面我们来看看是如何一步一步来构建我们的项目的，重点是开发流程以及遇到问题的解决过程。<strong> 建议：可以看一遍 <a href=#/docs/api>使用教程</a> ，然后再来看下面的实操过程，遇到不理解的再回头看文档，然后来理解开发过程，这样往复思考效果会更好一些。</strong></p> <h3 id=->初始化项目</h3> <p>首先，项目定位：</p> <ul> <li>支持 IE8 的 PC WEB 端（只是为了显示兼容性）</li> <li>支持 markdown 编写文档</li> </ul> <pre><code class=\"hljs shell\"><span class=hljs-meta>#</span><span class=bash> 初始化项目</span>\ngem-mine gem-mine-docs\n<span class=hljs-meta>\n#</span><span class=bash> 开始开发</span>\ncd gem-mine\nnpm start\n</code></pre> <p>这里不采用任何 UI 组件库，毕竟只是一个文档系统。如果想在 IE8 下开发预览，请执行 npm start --hot=false，因为 IE8 不支持热加载。更多命令，请参考：<a href=#/docs/api/command>命令行</a></p> <h3 id=->规划模块与路由</h3> <p>模块分为：</p> <ul> <li>首页</li> <li>文档</li> </ul> <p>它们共有头部和底部，头部导航去各个文档的分类，底部则是一些链接地址。</p> <p>因此路由就按此来划分，由于首页仅一个页面，就放在 <code>src/global/routes/index.js</code> 中配置，文档涉及了很多页面，独立拆分出 <code>src/global/routes/docs.js</code> 来配置。我们并不急于如何编写这个文件如何，可以新建出这个路由文件，以及在 components 下建立 <code>home</code> 和 <code>docs</code> 两个目录，作为后续开发的基础。那现在的增加的目录结构为（不变的省略）：</p> <pre><code class=\"hljs text\">├── src\n│     ├── components\n│     │     ├── home\n│     │     ├── docs\n│     ├── <span class=hljs-keyword>global</span>\n│     │     ├── routes\n│     │     │     ├── docs.js\n</code></pre> <p>由于项目中没有涉及到网络请求，因此这一块的配置在此项目中没有体现。</p> <h3 id=->布局开发</h3> <p>头部、底部是公用的，我们在 <code>src/components/common</code> 下分别建立 header、footer。</p> <pre><code class=\"hljs text\">├── src\n│     ├── components\n│     │     ├── common\n│     │     │     ├── <span class=hljs-selector-tag>header</span>\n│     │     │     ├── footer\n</code></pre> <p>这里提一下命名推荐的规范：<strong>目录一律用小写，jsx 组件除了 index.jsx 外采用驼峰命名</strong>，例如：footer 下的 index.jsx、Links.jsx。<strong>组件对外暴露的默认是 index.jsx</strong>。导入时只要到目录一级即可：</p> <pre><code class=\"hljs jsx\"><span class=hljs-keyword>import</span> Footer <span class=hljs-keyword>from</span> <span class=hljs-string>'components/common/footer'</span>\n</code></pre> <p>接着就可以根据 react 开发方式，开发 header、footer，其中的样式采用 scss 模块化开发。</p> <p>开发完毕后，在 <code>src/App.jsx</code> 引入，进行布局：</p> <pre><code class=\"hljs jsx\"><span class=hljs-keyword>import</span> Header <span class=hljs-keyword>from</span> <span class=hljs-string>'components/common/header'</span>\n<span class=hljs-keyword>import</span> Footer <span class=hljs-keyword>from</span> <span class=hljs-string>'components/common/footer'</span>\n\n<span class=hljs-keyword>const</span> App = <span class=hljs-function><span class=hljs-params>props</span> =&gt;</span> {\n  <span class=hljs-keyword>return</span> (\n    <span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>className</span>=<span class=hljs-string>\"main\"</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>Header</span> /&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>className</span>=<span class=hljs-string>\"body\"</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>Routes</span> /&gt;</span>\n      <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>Footer</span> /&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span></span>\n  )\n}\n</code></pre> <p>中间的一块留给路由填充组件使用</p> <h3 id=->开发首页</h3> <p>首页希望做的酷炫一些，加入一些动画。同时希望路由切换后切回来，可以保持动画后的状态，因此状态保存在 redux 中，也就是被脚手架提供的 model 托管。</p> <p>经过一些选型比较（依据：兼容性、易用性、维护性），动画库选择 <a href=https://github.com/google-fabric/velocity-react target=_blank>velocity-react@1.2.2</a>：</p> <pre><code class=\"hljs shell\"><span class=hljs-selector-tag>npm</span> <span class=hljs-selector-tag>i</span> <span class=hljs-selector-tag>velocity-react</span>@<span class=hljs-keyword>1</span>.<span class=hljs-keyword>2</span>.<span class=hljs-keyword>2</span> --save\n</code></pre> <p>velocity-react 在 IE8 下的动画需要依赖 jquery，因此引进 jquery-1.9.0（最后支持 IE8 的版本），引入工作需要进行以下三步：</p> <ul> <li>下载 jquery-1.9.0（压缩后的版本），放入 public 目录</li> <li>修改 <code>config/webpack.js</code>，将 jquery-1.9.0.js 加入到 additional 数组中，这样 webpack 编译时就会将其拷贝到目标目录中</li> <li>修改 <code>public/index.html</code>，使用 IE 条件表达式，用 script 标签引入 jquery</li> </ul> <p>然后重新 npm start 启动项目（修改了 webpack 的配置需要重新启动才能生效）</p> <p>此时，home 目录为：</p> <pre><code class=\"hljs text\">├── src\n│     ├── components\n│     │     ├── home\n│     │     │     ├── index<span class=hljs-selector-class>.jsx</span>\n│     │     │     ├── model<span class=hljs-selector-class>.js</span>\n│     │     │     ├── style.scss\n</code></pre> <p>这种标准结构，后续可以考虑加入命令行功能来生成。开发过程中，又拆分出 Console、Info、Logo 三个子组件。Console 组件用到了打字效果，同样经过选型比较，选择了 <code>react-typist</code>。但其不兼容 IE8，此时有几种解决方案：</p> <ol> <li>简单粗暴：使用其源码，将其放入 src，利用脚手架可以处理 IE8 的能力</li> <li>fork 该项目，修改源码，使其源码兼容 IE8，然后 npm 通过 github 来安装</li> <li>修改脚手架，对该包特殊处理</li> </ol> <p>常规的做法是第 2 种，但这里采用了第一种（因为这个 lib 功能比较简单，基本没有后续维护的需求），将其放在了 <code>src/components/common/typist</code>。</p> <p>编写完代码后，修改 <code>src/global/routes/index.js</code>，添加 home 对应的路由，然后浏览器中就能看到酷炫的首页了。</p> <h3 id=->文档开发</h3> <p>为了解析 markdown 文件并将其转化为 jsx，借助了 <code>marked</code> 这个库，同时为了显示代码语法高亮，结合了 <code>highlight.js</code>。这里自定义了一个 webpack 的 loader 来处理 <code>config/webpack/loaders/markdown.js</code>，同时将该 loader 加入 <code>config/webpack.js</code> 的 loaders 字段。同样由于修改了 webpack 配置，需要重新 npm start --hot 启动。</p> <p>解析来就是在 src/components/docs 目录下撸 markdown 文档了。文档具有二级目录，因此使用了子路由功能，鉴于各个文档结构布局的一致性，这里用了一些小技巧，提供了 Layout.jsx 进行封装。</p> <pre><code class=\"hljs text\">├── src\n│     ├── components\n│     │     ├── docs\n│     │     │     ├── api\n│     │     │     │     ├── <span class=hljs-keyword>markdown </span>files\n│     │     │     ├── guide\n│     │     │     │     ├── <span class=hljs-keyword>markdown </span>files\n│     │     │     ├── ...\n│     │     │     ├── Layout.jsx\n│     │     │     ├── style.scss\n</code></pre> <p>然后处理路由，配置对应文档的地址，<code>src/global/routes/docs.js</code> 看起来和常规路由配置有些不同，只是利用了 <code>src/global/routes/docs-helper.js</code> 提供的函数对 markdown 和 Layout 进行统一处理少写点重复的代码，仅此而已。</p> <h3 id=->收获</h3> <p>使用 gem-mine 开发了 gem-mine-docs，一方面验证了其开发的便利性、灵活度，另一方面也反哺了 gem-mine，发现了脚手架一些 bug，并提出一些基础需求让脚手架改进增强。</p> <p>希望你在 gem-mine 的过程中，也能提出思考后的建设性意见，欢迎在 <a href=https://github.com/gem-mine/gem-mine/issues target=_blank> issues</a> 提出问题，如果你可以 pull request 进行贡献，那自然是极好的。只有良好的互动，才是一个健康的程序，健康的生态。</p> ";

