	module.exports = "<h1 id=model>model</h1> <p>所谓 model，是一个整合了 redux 的 state、action、reducer（含同步、异步）、dispatch、contant 管理功能的集合体，开发者不要为了修改一个小功能频繁的在多个文件甚至目录之间切换，带来体力和脑力的开销。</p> <p>model 由 <a href=https://github.com/gem-mine/cat-eye target=_blank>cat-eye</a> 提供，model 层设计重度参考了 <a href=https://github.com/mirrorjs/mirror target=_blank>mirror</a>，如果你熟悉 mirror，那么你会觉得这里的设计格外亲切。遇到下面一些概念的迷惑（actions、reducers、effects），亦可以参考 mirror 的文档 <a href=https://github.com/mirrorjs/mirror/blob/master/docs/zh/api.md target=_blank>mirror API</a></p> <p><img src=" + __webpack_require__(145) + " /></p> <h2 id=-model>定义 model</h2> <p>model <strong>有且仅有 4 个属性值，分别是：name，state，reducers，effects</strong>，我们通常这样定义一个 model：</p> <pre><code class=\"hljs javascript\">import ce <span class=hljs-keyword>from</span> 'cat-eye'\n\nce.model({\n  name: '<span class=hljs-keyword>user</span>',\n  <span class=hljs-keyword>state</span>: {},\n  reducers: {},\n  effects: {}\n})\n</code></pre> <ul> <li>name 的作用 name 是 model 的名称，用于创建命名空间，该命名空间起到了两个作用（后续会看到如何使用）：<ul> <li>将模块自己的 state 挂载到此命名空间下</li> <li>将模块自己的 actions 挂载到此命名空间下</li> </ul> </li> <li>state：<strong>model 的初始 state</strong>，这是 redux reducer 中标准的 initialState</li> <li>reducers：reducers 由一个个 function 组成，包含了 3 个方面的能力：<ul> <li>用于创建 redux 中的 reducer</li> <li>自动根据[model name + reducer name] 来创建 action，因此一个 reducer 对应的是一个 action</li> <li>将 action 挂载在 actions[model] 下，通过 reducer name 来访问</li> </ul> </li> </ul> <blockquote> <p>注意，reducer 必须返回一个 state，这是 redux reducer 要求的</p> </blockquote> <pre><code class=\"hljs javascript\"><span class=hljs-comment>// src/global/model/user.js</span>\n<span class=hljs-keyword>import</span> ce <span class=hljs-keyword>from</span> <span class=hljs-string>'cat-eye'</span>\nce.model({\n  <span class=hljs-attr>name</span>: <span class=hljs-string>'user'</span>,\n  <span class=hljs-attr>state</span>: {\n    <span class=hljs-attr>age</span>: <span class=hljs-number>18</span>,\n    <span class=hljs-attr>nickname</span>: <span class=hljs-string>'jerry'</span>\n  },\n  <span class=hljs-attr>reducers</span>: {\n    growUp() {\n      <span class=hljs-keyword>return</span> <span class=hljs-keyword>this</span>.setField({\n        <span class=hljs-attr>age</span>: <span class=hljs-function><span class=hljs-params>v</span> =&gt;</span> v + <span class=hljs-number>1</span>\n      })\n    }\n  }\n})\n\n<span class=hljs-comment>// 在你需要的地方，通常是在 jsx 或者 model 的 effects 中调用</span>\n<span class=hljs-comment>// 例如 某个 jsx 中</span>\n<span class=hljs-keyword>import</span> { actions } <span class=hljs-keyword>from</span> <span class=hljs-string>'cat-eye'</span>\n\n<span class=hljs-keyword>export</span> <span class=hljs-keyword>default</span> props =&gt; {\n  <span class=hljs-keyword>return</span> <span class=xml><span class=hljs-tag>&lt;<span class=hljs-name>button</span> <span class=hljs-attr>onClick</span>=<span class=hljs-string>{actions.user.growUp}</span>&gt;</span>click me<span class=hljs-tag>&lt;/<span class=hljs-name>button</span>&gt;</span></span>\n}\n</code></pre> <p>上面代码中，注意到 growUp 这个 reducer 中，使用了 this.setField，这是一个强大易用高效的工具，内部结合了 轻量的 immutable 功能，先不要着急如何使用，本文后面会为你介绍。</p> <ul> <li>effects：一般异步的操作（有副作用的操作，非纯函数）放在 effects，而不放在 reducers 中。对于业务来说，大部分操作都是异步请求，因此 effects 更经常使用。另外，effects 无须返回 state，通常我们会把异步操作返回，也就是返回一个 promise。除此之外，effects 看起来和 reducers 并没有什么两样，定义的方法同样会挂载到 actions 上。</li> </ul> <p>reducers、effects 更详细的用法后面也会介绍。</p> <h2 id=-model>使用 model</h2> <h3 id=model->model 的加载</h3> <p>model 是自动引入到系统的，但需要满足下面其中一个原则：</p> <ul> <li>全局的 model 放在 <code>src/global/model</code> 目录下，命名可以自定义，例如放 user.js 这种各个模块都可能使用到的公用 model</li> <li>业务的 model 放在业务自己的目录下，必须取名为 <code>model.js</code></li> </ul> <h3 id=actions->actions 的使用</h3> <p>在 model 中定义好 reducers、effects 后，这些方法就会挂载在 actions 下，可以直接拿来使用：</p> <pre><code class=\"hljs javascript\"><span class=hljs-keyword>import</span> { actions } <span class=hljs-keyword>from</span> <span class=hljs-string>'cat-eye'</span>\n</code></pre> <p>如果不需要进行加工，可以在 JSX 中直接调用对应的方法：</p> <pre><code>&lt;SomeComponent onClick={actions<span class=hljs-selector-class>.user</span><span class=hljs-selector-class>.growUp</span>} /&gt;\n</code></pre><p>需要进行一些数据操作，可以在组件的方法中定义：</p> <pre><code><span class=hljs-keyword>import</span> <span class=hljs-type>React</span>, {<span class=hljs-type>Component</span>} from <span class=hljs-symbol>'reac</span>t'\n<span class=hljs-keyword>import</span> {actions} from <span class=hljs-symbol>'cat</span>-eye'\n\n<span class=hljs-class><span class=hljs-keyword>class</span> <span class=hljs-title>SomeComponent</span> <span class=hljs-keyword>extends</span> <span class=hljs-title>Component</span> </span>{\n  handleClick = () =&gt; {\n    <span class=hljs-comment>// some code ...</span>\n    actions.user.growUp()\n    <span class=hljs-comment>// some code ...</span>\n  }\n  render() {\n    <span class=hljs-keyword>return</span> (\n      &lt;button onClick={<span class=hljs-keyword>this</span>.handleClick}&gt;click me&lt;/button&gt;\n    )\n  }\n}\n</code></pre><h3 id=-model-smart-actions>组件中使用 model：smart、actions</h3> <p>组件中使用 model 通过 cat-eye 提供的 smart，这个是 react-redux connect 易用版：</p> <pre><code class=\"hljs javascript\"><span class=hljs-keyword>import</span> { smart } from 'cat-eye'\n\n<span class=hljs-title>export</span> <span class=hljs-keyword>default</span> smart(<span class=hljs-title>mapStateToProps</span>, <span class=hljs-title>mapDispathToProps</span>)(<span class=hljs-type>SomeComponent</span>)\n</code></pre> <p>这样看起来和 connect 似乎并无差别，重点是 mapDispatchToProps 可以直接取到 this.props 的值作为参数，来看下 mapDispatchToProps 的使用：</p> <pre><code class=\"hljs javascript\"><span class=hljs-keyword>import</span> { actions } <span class=hljs-keyword>from</span> <span class=hljs-string>'cat-eye'</span>\n<span class=hljs-comment>// mapDispatchToProps 的 props 即这个组件的 this.props</span>\n<span class=hljs-keyword>export</span> <span class=hljs-keyword>default</span> smart(<span class=hljs-literal>null</span>, <span class=hljs-function><span class=hljs-params>props</span> =&gt;</span> {\n  <span class=hljs-comment>// 一般 mapDispatchToProps 会调用 actions 提供的方法，以此来更新 state</span>\n  <span class=hljs-keyword>const</span> act = actions.user\n  <span class=hljs-keyword>return</span> {\n    growUp: act.growUp\n  }\n})(SomeComponent)\n</code></pre> <p>而 mapStateToProps 和 connect 并无两异：</p> <pre><code>// mapStateToProps\n(<span class=hljs-keyword>state</span>, ownProps) =&gt; {\n  const s = <span class=hljs-keyword>state</span>.<span class=hljs-keyword>user</span>;\n  return {\n    // the fields you want <span class=hljs-keyword>to</span> map\n  }\n}\n</code></pre><p>cat-eye 同样将 connect 暴露了出来，但推荐你使用 smart。</p> <h3 id=setfield>setField</h3> <p>setFiled 依赖于 <a href=https://github.com/gem-mine/zero-immutable target=_blank>zero-immutable</a> 的实现，将 immutable 的能力内置在 gem-mine 中，既提高了性能，也很大程度降低了代码的繁琐。</p> <h4 id=setfield->setField 接受的参数</h4> <p>setField(object)：接受一个 key-value 的对象作为参数：</p> <pre><code class=\"hljs javascript\">{\n  <span class=hljs-attribute>name</span>: <span class=hljs-string>'tom'</span>,\n  age: <span class=hljs-number>20</span>\n}\n</code></pre> <p>setField 接受的 key-value 不支持多级，对于 state 是多层级的情况，可以这样处理：</p> <pre><code class=\"hljs javascript\"><span class=hljs-comment>// state</span>\n{\n  <span class=hljs-keyword>name</span>: <span class=hljs-string>'tom'</span>,\n  family: [{\n    <span class=hljs-keyword>name</span>: <span class=hljs-string>'lily'</span>\n  }, {\n    <span class=hljs-keyword>name</span>: <span class=hljs-string>'jerry'</span>\n  }],\n  job: {\n    <span class=hljs-built_in>title</span>: <span class=hljs-string>'manager'</span>\n  }\n}\n\n<span class=hljs-comment>// setField 参数</span>\n{\n  <span class=hljs-string>'family.1.name'</span>: <span class=hljs-string>'pig'</span>, <span class=hljs-comment>// 将 jerry 修改</span>\n  <span class=hljs-string>'job.title'</span>: <span class=hljs-string>'boss'</span>\n}\n</code></pre> <p>key-value 的 value 还可以是一个 function：</p> <pre><code>{\n  <span class=hljs-attribute>age</span>: age =&gt; age + <span class=hljs-number>1</span>\n}\n</code></pre><h4 id=actions-setfield>actions 内置 setField</h4> <p>所有的 model，其 reducers 都会默认注册一个 setField 方法，这样 actions 上就会有该 model 的 setField 方法。正是由于这个方法的存在，导致了 reducers 存在感非常低，大部分设值操作都变得多余，直接通过 setField 进行即可。</p> <pre><code class=\"hljs javascript\">import { actions } <span class=hljs-keyword>from</span> 'cat-eye'\n\n// 在某个组件中直接修改 <span class=hljs-keyword>user</span> <span class=hljs-keyword>state</span>，无须定义 reducer\nactions.<span class=hljs-keyword>user</span>.<span class=hljs-built_in>set</span>Field({\n  name: 'tom',\n  age: age =&gt; age + <span class=hljs-number>1</span>\n})\n</code></pre> <p>如果是纯粹的 redux 开发，这里要定义常量，定义 state，定义 action，实现 reducer，最后才在组件中进行 dispatch（可能还需要 connect），这里就简化成了一步。</p> <h4 id=-reducers->在 reducers 中使用</h4> <p><strong>重要：reducers 中定义的 function 请不要使用箭头函数，否则会丢失 this</strong></p> <p>reducers 的 function 是这样定义的：</p> <pre><code class=\"hljs javascript\">// <span class=hljs-keyword>user</span> model 的 reducers\n\n{\n  // data 是 actions.<span class=hljs-keyword>user</span>.growUp(data) 传入的\n  // getState 可以获取到 redux 的整个 <span class=hljs-keyword>state</span>\n  growUp(data, getState) {\n    // reducer 必须 return 一个新的 <span class=hljs-keyword>state</span>，<span class=hljs-built_in>set</span>Field 就有这个能力\n    return this.<span class=hljs-built_in>set</span>Field({\n      // ...\n    })\n  }\n}\n</code></pre> <p>可以看到，reducer 中的 this 具有 setField 方法，可以对自己的 model 的 state 进行更新。对于获取 state 的，分为：</p> <ul> <li>获取自己 model 的 state：通过 <code>this.getState()</code> 就可以拿到</li> <li>获取所有的 state：通过第二个参数 <code>getState()</code> 可以拿到，然后根据命名空间来获取，例如 getState().book.name</li> </ul> <h4 id=-effects->在 effects 中使用</h4> <p>effects 中的使用和 reducers 中完全一样，只是无须返回 state。同样，<strong>请不要使用箭头函数</strong>。一般在 effects 中会使用到 网络请求，这部分文档参看 <a href=#/docs/api/request>网络请求</a></p> <pre><code class=\"hljs javascript\"><span class=hljs-comment>// user model 的 effects</span>\n<span class=hljs-keyword>import</span> {request} <span class=hljs-keyword>from</span> <span class=hljs-string>'cat-eye'</span>\n<span class=hljs-keyword>const</span> {api} = request\n\n{\n  <span class=hljs-comment>// data 是 actions.user.fetchSomething(data) 传入的</span>\n  <span class=hljs-comment>// getState 可以获取到 redux 的整个 state</span>\n  fetchSomething(data, getState) {\n    api.post({}).then(<span class=hljs-function><span class=hljs-params>data</span> =&gt;</span> {\n      <span class=hljs-keyword>this</span>.setField({\n        <span class=hljs-comment>// ...</span>\n      })\n    })\n  }\n}\n</code></pre> <p>获取 state（自己或者全局）和 reducers 是一致的，分别是通过 this.getState() 和 函数的第二个参数 getState() 来获得。</p> ";

