	module.exports = "<h1 id=->路由</h1> <p>gem-mine 内置 react-router 4，并提供了基于 react router 4 的封装，具有以下特性：</p> <ul> <li>使用 JSON 方式对路由进行配置</li> <li>支持多级子路由</li> <li>支持默认路由</li> <li>支持权限拦截</li> </ul> <h2 id=->配置与使用</h2> <p>路由配置文件存放在 <code>src/global/routes</code> 目录下，一般所有配置都放置在 <code>index.js</code> 中，当然你可以进行拆分成多个文件来处理来减少冲突。</p> <p>路由配置包括两个部分：<strong>通用配置</strong> 和 <strong>路由注册</strong>。</p> <h3 id=->通用配置</h3> <p>通用配置用来为路由组件注入 props，以及设置 403 无权限，404 没找到路由的默认组件配置。这个配置是通过 cat-eye 提供的 router.config 进行：</p> <pre><code class=\"hljs javascript\"><span class=hljs-keyword>import</span> { router } <span class=hljs-keyword>from</span> <span class=hljs-string>'cat-eye'</span>\n<span class=hljs-keyword>import</span> NotFound <span class=hljs-keyword>from</span> <span class=hljs-string>'components/common/exception/404'</span>\n<span class=hljs-keyword>import</span> Forbidden <span class=hljs-keyword>from</span> <span class=hljs-string>'components/common/exception/403'</span>\n\n<span class=hljs-comment>// 通用配置</span>\nrouter.config({\n  <span class=hljs-comment>// 为路由注入 props，主要是为了做权限判断时使用，路由配置时可以看到使用</span>\n  mapStateToProps: <span class=hljs-function><span class=hljs-params>state</span> =&gt;</span> {\n    <span class=hljs-keyword>return</span> {\n      <span class=hljs-attr>user</span>: state.user\n    }\n  },\n  <span class=hljs-comment>// 设置默认的 403，404 组件（可选，默认的403、404 无样式，较为简陋）</span>\n  components: {\n    NotFound,\n    Forbidden\n  }\n})\n</code></pre> <h3 id=->路由注册</h3> <p>通过 router.register 来注册路由，接受的参数是一个 object，一个相对完整的模板如下：</p> <pre><code class=\"hljs javascript\"><span class=hljs-string>import</span> <span class=hljs-string>{</span> <span class=hljs-string>router</span> <span class=hljs-string>}</span> <span class=hljs-string>from</span> <span class=hljs-string>'cat-eye'</span>\n\n<span class=hljs-string>//</span> <span class=hljs-string>注册路由</span>\n<span class=hljs-string>router.register({</span>\n<span class=hljs-attr>  login:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>    path:</span> <span class=hljs-string>LOGIN_PATH,</span>\n<span class=hljs-attr>    component:</span> <span class=hljs-string>Login,</span>\n<span class=hljs-attr>    description:</span> <span class=hljs-string>'登录页'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>    index:</span> <span class=hljs-literal>true</span> <span class=hljs-string>//</span> <span class=hljs-string>根路由下默认显示</span>\n  <span class=hljs-string>},</span>\n<span class=hljs-attr>  main:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>    path:</span> <span class=hljs-string>'/'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>    permission:</span> <span class=hljs-string>props</span> <span class=hljs-string>=&gt;</span> <span class=hljs-string>{</span>\n      <span class=hljs-string>if</span> <span class=hljs-string>(getIn(props,</span> <span class=hljs-string>'user.token'</span><span class=hljs-string>))</span> <span class=hljs-string>{</span>\n        <span class=hljs-string>return</span> <span class=hljs-literal>true</span>\n      <span class=hljs-string>}</span>\n      <span class=hljs-string>return</span> <span class=hljs-string>&lt;Redirect</span> <span class=hljs-string>to={{</span> <span class=hljs-attr>pathname:</span> <span class=hljs-string>LOGIN_PATH</span> <span class=hljs-string>}}</span> <span class=hljs-string>/&gt;</span>\n    <span class=hljs-string>},</span>\n    <span class=hljs-string>//</span> <span class=hljs-string>子路由</span>\n<span class=hljs-attr>    sub:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>      home:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>        path:</span> <span class=hljs-string>'/home'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>        component:</span> <span class=hljs-string>Home,</span>\n<span class=hljs-attr>        description:</span> <span class=hljs-string>'首页'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>        index:</span> <span class=hljs-literal>true</span> <span class=hljs-string>//</span> <span class=hljs-string>子路由如果要默认显示，path</span> <span class=hljs-string>设置成</span> <span class=hljs-string>/，并且设置</span> <span class=hljs-attr>exact:</span> <span class=hljs-literal>true</span><span class=hljs-string>。或者设置</span> <span class=hljs-string>index</span> <span class=hljs-string>为</span> <span class=hljs-literal>true</span>\n      <span class=hljs-string>},</span>\n<span class=hljs-attr>      about:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>        path:</span> <span class=hljs-string>'/about'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>        component:</span> <span class=hljs-string>About,</span>\n<span class=hljs-attr>        description:</span> <span class=hljs-string>'关于页'</span>\n      <span class=hljs-string>},</span>\n<span class=hljs-attr>      topic:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>        path:</span> <span class=hljs-string>'/topics'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>        redirect:</span> <span class=hljs-string>'main.topic.list'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>        module:</span> <span class=hljs-string>{</span>\n          <span class=hljs-string>//</span> <span class=hljs-string>子模块</span>\n<span class=hljs-attr>          list:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>            description:</span> <span class=hljs-string>'话题列表页'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>            path:</span> <span class=hljs-string>'/list'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>            component:</span> <span class=hljs-string>TopicList</span>\n          <span class=hljs-string>},</span>\n<span class=hljs-attr>          add:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>            description:</span> <span class=hljs-string>'添加话题页面'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>            path:</span> <span class=hljs-string>'/add'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>            component:</span> <span class=hljs-string>TopicAdd</span>\n          <span class=hljs-string>},</span>\n<span class=hljs-attr>          detail:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>            description:</span> <span class=hljs-string>'话题详细页'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>            path:</span> <span class=hljs-string>'/:id'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>            component:</span> <span class=hljs-string>TopicItem</span>\n          <span class=hljs-string>}</span>\n        <span class=hljs-string>}</span>\n      <span class=hljs-string>},</span>\n<span class=hljs-attr>      admin:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>        description:</span> <span class=hljs-string>'管理路由'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>        path:</span> <span class=hljs-string>'/admin'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>        component:</span> <span class=hljs-string>Admin,</span> <span class=hljs-string>//</span> <span class=hljs-string>存在子路由，需要有个组件来放置路由</span>\n<span class=hljs-attr>        permission:</span> <span class=hljs-string>props</span> <span class=hljs-string>=&gt;</span> <span class=hljs-string>{</span>\n          <span class=hljs-string>if</span> <span class=hljs-string>(props.user.name</span> <span class=hljs-string>===</span> <span class=hljs-string>ADMIN</span> <span class=hljs-string>||</span> <span class=hljs-string>props.user.name</span> <span class=hljs-string>===</span> <span class=hljs-string>SUPER)</span> <span class=hljs-string>{</span>\n            <span class=hljs-string>return</span> <span class=hljs-literal>true</span>\n          <span class=hljs-string>}</span>\n          <span class=hljs-string>return</span> <span class=hljs-literal>false</span>\n        <span class=hljs-string>},</span>\n<span class=hljs-attr>        sub:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>          dashboard:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>            path:</span> <span class=hljs-string>'/dashboard'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>            description:</span> <span class=hljs-string>'管理首页'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>            index:</span> <span class=hljs-literal>true</span><span class=hljs-string>,</span> <span class=hljs-string>//</span> <span class=hljs-string>作为默认显示的子路由</span>\n<span class=hljs-attr>            redirect:</span> <span class=hljs-string>{</span>\n              <span class=hljs-string>//</span> <span class=hljs-string>带参数的</span> <span class=hljs-string>redirect</span>\n<span class=hljs-attr>              key:</span> <span class=hljs-string>'main.admin.dashboard.x'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>              params:</span> <span class=hljs-string>{</span> <span class=hljs-attr>name:</span> <span class=hljs-string>'tom'</span> <span class=hljs-string>}</span>\n            <span class=hljs-string>},</span>\n<span class=hljs-attr>            module:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>              x:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>                path:</span> <span class=hljs-string>'/x'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>                component:</span> <span class=hljs-string>X</span>\n              <span class=hljs-string>},</span>\n<span class=hljs-attr>              y:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>                path:</span> <span class=hljs-string>'/y'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>                component:</span> <span class=hljs-string>Y</span>\n              <span class=hljs-string>}</span>\n            <span class=hljs-string>}</span>\n          <span class=hljs-string>},</span>\n<span class=hljs-attr>          super:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>            description:</span> <span class=hljs-string>'超级管理员页面'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>            path:</span> <span class=hljs-string>'/super'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>            component:</span> <span class=hljs-string>Super,</span>\n<span class=hljs-attr>            permission:</span> <span class=hljs-string>props</span> <span class=hljs-string>=&gt;</span> <span class=hljs-string>{</span>\n              <span class=hljs-string>if</span> <span class=hljs-string>(props.user.name</span> <span class=hljs-string>===</span> <span class=hljs-string>SUPER)</span> <span class=hljs-string>{</span>\n                <span class=hljs-string>return</span> <span class=hljs-literal>true</span>\n              <span class=hljs-string>}</span>\n              <span class=hljs-string>return</span> <span class=hljs-string>&lt;Forbidden</span> <span class=hljs-string>message={`请将用户名改为</span> <span class=hljs-string>${SUPER}`}</span> <span class=hljs-string>/&gt;</span>\n            <span class=hljs-string>}</span>\n          <span class=hljs-string>},</span>\n<span class=hljs-attr>          log:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>            description:</span> <span class=hljs-string>'日志'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>            path:</span> <span class=hljs-string>'/logs'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>            module:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>              list:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>                description:</span> <span class=hljs-string>'日志列表'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>                path:</span> <span class=hljs-string>'/'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>                exact:</span> <span class=hljs-literal>true</span><span class=hljs-string>,</span>\n<span class=hljs-attr>                component:</span> <span class=hljs-string>LogList</span>\n              <span class=hljs-string>},</span>\n<span class=hljs-attr>              detail:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>                description:</span> <span class=hljs-string>'日志详情'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>                path:</span> <span class=hljs-string>'/:id'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>                component:</span> <span class=hljs-string>LogItem</span>\n              <span class=hljs-string>}</span>\n            <span class=hljs-string>}</span>\n          <span class=hljs-string>}</span>\n        <span class=hljs-string>}</span>\n      <span class=hljs-string>}</span>\n    <span class=hljs-string>}</span>\n  <span class=hljs-string>}</span>\n<span class=hljs-string>})</span>\n</code></pre> <p>路由中的每一项，可能由下面的若干个配置构成：</p> <ul> <li>path: 路由对应的 url 地址，嵌套的路由（包括 sub 和 module）会祖先路由的 path 作为前缀拼接</li> <li>component：路由对应的组件</li> <li>permission：权限拦截函数，返回 true 表示有权限。返回 false 则渲染默认无权限组件，返回一个组件时是表示无权限对应的自定义组件。嵌套的路由会继承祖先的 permission 权限拦截函数，因此只有祖先 permission 返回 true 后才会进入目标路由</li> <li>description：描述，相当于一个注释作用</li> <li>index：是否作为默认子路由</li> <li>redirect：进入该路由后自动跳转到指定路由。<strong>注意：redirect 不能和 sub 同级别存在，也就是某一项路由中，redirect 和 sub 不能同时存在</strong>，因为如果 redirect 的目标是自己的子路由，而子路由渲染必然需要经过祖先路由，又会触发 redirect，从而死循环。虽然 redirect 一般是结合 module 使用，表示跳转进入某个平级路由，但 redirect 其实是可以跳转进入任意的其他路由。</li> <li>sub：子路由，其下配置也是一个路由配置</li> <li>module：平级模块路由，其下配置也是一个路由配置</li> <li>exact：唯一匹配，否则”/“和”/page1”都会匹配到 path 为&quot;/&quot;的路由，制定 exact 后，&quot;/page1&quot;就不会再匹配到&quot;/&quot;了</li> </ul> <h4 id=-sub-module->嵌套路由：sub 和 module 的区别</h4> <p>sub 和 module 都属于嵌套路由，会从祖先路由那里继承到 path 和 permission。其区别在于 sub 是子路由，需要一个容器作为承载，路由变化时，只有指定部分的内容会发生变化。 module 我们称之为平级模块，只是为了较少 path 和 permission 书写而已。module 无论嵌套多少级他们之间都是平级的，是共享操作一个区域的内容。</p> <p>子路由 sub 被 <code>&lt;Routes&gt;</code> 组件接收，是这样使用的：</p> <pre><code class=\"hljs javascript\"><span class=hljs-comment>// 使用顶级路由</span>\n<span class=hljs-params>&lt;Routes/&gt;</span>\n\n<span class=hljs-comment>// 使用某个子路由</span>\n<span class=hljs-params>&lt;Routes path=\"main.admin\"/&gt;</span>\n</code></pre> <h4 id=->默认路由</h4> <p>默认路由是针对 sub 的，当给某个 sub 的元素指定 index:true 时，如果 url 对应的是父路由的 path，该子路由会作为默认显示。例如上面的配置，当访问 / 时，permission 通过时，由于其 home 子路由具有 index:true，因此会默认显示 home 对应的内容。当然，访问 /home 也是一样显示 home。</p> <p>默认路由还有另外一种写法，即子路由的 path 为 / 时，就是作为默认子路由显示，此时通常需要增加一个 exact:true 防止路由冲突，例如上面配置中的 main.admin.log.list。</p> <p>如果是 module，想默认进入某个路由，可以使用 redirect 进行跳转。</p> <h4 id=->路由的拆分</h4> <p>所有路由都写在 <code>src/global/routes/index.js</code> 下的话，可能会导致该文件变得很大，多人协作时容易引起冲突。可以利用 js 的模块性质进行拆分，通常根据功能块进行拆分：</p> <pre><code class=\"hljs javascript\"><span class=hljs-string>import</span> <span class=hljs-string>topicRoutes</span> <span class=hljs-string>from</span> <span class=hljs-string>'./main.topic'</span>\n<span class=hljs-string>import</span> <span class=hljs-string>adminRoutes</span> <span class=hljs-string>from</span> <span class=hljs-string>'./main.admin'</span>\n\n<span class=hljs-string>router.register({</span>\n<span class=hljs-attr>  login:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>    path:</span> <span class=hljs-string>LOGIN_PATH,</span>\n<span class=hljs-attr>    component:</span> <span class=hljs-string>Login,</span>\n<span class=hljs-attr>    description:</span> <span class=hljs-string>'登录页'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>    index:</span> <span class=hljs-literal>true</span> <span class=hljs-string>//</span> <span class=hljs-string>根路由下默认显示</span>\n  <span class=hljs-string>},</span>\n<span class=hljs-attr>  main:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>    path:</span> <span class=hljs-string>'/'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>    permission:</span> <span class=hljs-string>props</span> <span class=hljs-string>=&gt;</span> <span class=hljs-string>{</span>\n      <span class=hljs-string>if</span> <span class=hljs-string>(getIn(props,</span> <span class=hljs-string>'user.token'</span><span class=hljs-string>)</span> <span class=hljs-string>&amp;&amp;</span> <span class=hljs-string>getIn(props,</span> <span class=hljs-string>'user.name'</span><span class=hljs-string>))</span> <span class=hljs-string>{</span>\n        <span class=hljs-string>return</span> <span class=hljs-literal>true</span>\n      <span class=hljs-string>}</span>\n      <span class=hljs-string>return</span> <span class=hljs-string>&lt;Redirect</span> <span class=hljs-string>to={{</span> <span class=hljs-attr>pathname:</span> <span class=hljs-string>LOGIN_PATH</span> <span class=hljs-string>}}</span> <span class=hljs-string>/&gt;</span>\n    <span class=hljs-string>},</span>\n    <span class=hljs-string>//</span> <span class=hljs-string>子路由</span>\n<span class=hljs-attr>    sub:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>      home:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>        path:</span> <span class=hljs-string>'/home'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>        component:</span> <span class=hljs-string>Home,</span>\n<span class=hljs-attr>        description:</span> <span class=hljs-string>'首页'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>        index:</span> <span class=hljs-literal>true</span>\n      <span class=hljs-string>},</span>\n<span class=hljs-attr>      about:</span> <span class=hljs-string>{</span>\n<span class=hljs-attr>        path:</span> <span class=hljs-string>'/about'</span><span class=hljs-string>,</span>\n<span class=hljs-attr>        component:</span> <span class=hljs-string>About,</span>\n<span class=hljs-attr>        description:</span> <span class=hljs-string>'关于页'</span>\n      <span class=hljs-string>},</span>\n<span class=hljs-attr>      topic:</span> <span class=hljs-string>topicRoutes,</span>\n<span class=hljs-attr>      admin:</span> <span class=hljs-string>adminRoutes</span>\n    <span class=hljs-string>}</span>\n  <span class=hljs-string>}</span>\n<span class=hljs-string>})</span>\n</code></pre> <p>topic 配置放在同目录下的 <code>main.topic.js</code> 中：</p> <pre><code class=\"hljs javascript\"><span class=hljs-selector-tag>export</span> <span class=hljs-selector-tag>default</span> {\n  <span class=hljs-attribute>path</span>: <span class=hljs-string>'/topics'</span>,\n  <span class=hljs-attribute>redirect</span>: <span class=hljs-string>'main.topic.list'</span>, <span class=hljs-comment>// 跳转到话题列表页（如果话题列表页path是 / 就不要 redirect）</span>\n  <span class=hljs-comment>// 子模块</span>\n  <span class=hljs-attribute>module</span>: {\n    <span class=hljs-attribute>list</span>: {\n      <span class=hljs-attribute>description</span>: <span class=hljs-string>'话题列表页'</span>,\n      <span class=hljs-attribute>path</span>: <span class=hljs-string>'/list'</span>,\n      <span class=hljs-attribute>component</span>: TopicList\n    },\n    <span class=hljs-attribute>add</span>: {\n      <span class=hljs-attribute>description</span>: <span class=hljs-string>'添加话题页面'</span>,\n      <span class=hljs-attribute>path</span>: <span class=hljs-string>'/add'</span>,\n      <span class=hljs-attribute>component</span>: TopicAdd\n    },\n    <span class=hljs-attribute>detail</span>: {\n      <span class=hljs-attribute>description</span>: <span class=hljs-string>'话题详细页'</span>,\n      <span class=hljs-attribute>path</span>: <span class=hljs-string>'/:id'</span>,\n      <span class=hljs-attribute>component</span>: TopicItem\n    }\n  }\n}\n</code></pre> <p>同样也将 admin 配置抽取，放在同目录下的 <code>main.admin.js</code> 中</p> <h3 id=->路由的使用</h3> <h4 id=->使用子路由</h4> <p>使用 <code>Routes</code> 组件</p> <pre><code class=\"hljs javascript\"><span class=hljs-comment>// 使用顶级路由</span>\n<span class=hljs-params>&lt;Routes/&gt;</span>\n\n<span class=hljs-comment>// 使用某个子路由</span>\n<span class=hljs-params>&lt;Routes path=\"main.admin\"/&gt;</span>\n</code></pre> <h4 id=->使用链接跳转</h4> <p>使用 <code>Link</code> 组件，一般结合 urlFor 来自动从路由配置中获取 url</p> <pre><code class=\"hljs javascript\">&lt;<span class=hljs-keyword>Link</span> <span class=hljs-keyword>to</span>={urlFor(<span class=hljs-string>'main.admin'</span>)}&gt;跳转&lt;/<span class=hljs-keyword>Link</span>&gt;\n\n<span class=hljs-comment>// 携带参数，id命中路径参数，没命中的 name 和 age 作为查询参数，因此得到 url: /topics/233?name=tom&amp;age22</span>\n&lt;<span class=hljs-keyword>Link</span> <span class=hljs-keyword>to</span>={urlFor(<span class=hljs-string>'main.topic.detail'</span>, {id: <span class=hljs-number>233</span>, name:<span class=hljs-string>'tom'</span>, age: <span class=hljs-number>22</span>})}&gt;\n</code></pre> <h4 id=->使用导航链接</h4> <p>对于导航性质，尤其是需要高亮处理的情况，建议使用 <code>NavLink</code> 替换 Link 组件。具体使用参看 <a href=https://reacttraining.com/react-router/web target=_blank>react-router 4 文档</a></p> <h4 id=-url->获取 url 参数</h4> <p>获取查询参数通过 cat-eye 提供的 <code>queryString</code> 工具对 search 参数进行解析，获取路径参数直接通过 props.match.params 来获取。</p> <p>例如上面路由中定义的 main.topic.detail 对应的 url：/topics/233?name=tom&amp;age22，其中的 233 就是路径参数（key 为 id），name 和 age 是查询参数，可以这样获取：</p> <pre><code class=\"hljs javascript\"><span class=hljs-keyword>import</span> { queryString } from <span class=hljs-string>'cat-eye'</span>\n\n<span class=hljs-keyword>export</span> <span class=hljs-keyword>default</span> props =&gt; {\n  <span class=hljs-keyword>const</span> { name } = queryString.parse(props.location.search)\n  <span class=hljs-keyword>const</span> { id } = props.match.params\n\n  <span class=hljs-comment>// ...</span>\n}\n</code></pre> <p>注意，如果 props 中没有 search 和 match，需要使用 <code>withRouter</code> 对组件进行包装。</p> <h3 id=-react-router-api>其他 react-router 的 API</h3> <p>cat-eye 对 <a href=https://reacttraining.com/react-router/web target=_blank>react-router 4 </a> 进行了封装，但也将其常用 API 暴露出来，包括：</p> <ul> <li>Route</li> <li>Redirect</li> <li>Switch</li> <li>Prompt</li> <li>withRouter</li> <li>Link</li> <li>NavLink</li> </ul> ";

