	module.exports = "<h1 id=->版本履历</h1> <ul> <li><p><code>2018年02月06日</code></p> <ul> <li>gem-mine 0.2.0<ul> <li>gem-mine update 能力升级，可以通过选择进行针对性的升级</li> <li>优化 git 工具安装的检测</li> </ul> </li> </ul> </li> <li><p><code>2018年02月05日</code></p> <ul> <li>gem-mine-template<ul> <li>提供 stylelint，并对开发模式下启用 cache 以及 dll plugin，提高编译速度</li> <li>提供 postcss 支持</li> </ul> </li> </ul> </li> <li><p><code>2018年01月25日</code></p> <ul> <li>cat-eye 0.3.6（latest）或 2.2.1（next）<ul> <li>提供 getState 方法，在任意地方直接获取 store</li> </ul> </li> </ul> </li> <li><p><code>2018年01月22日</code></p> <ul> <li>gem-mine-template<ul> <li>windows 下 webpack-dev-server 默认使用本机 IP 打开</li> </ul> </li> </ul> </li> <li><p><code>2018年01月18日</code></p> <ul> <li><p>gem-mine 0.1.9</p> <ul> <li>gem-mine update 命令自动处理增加的包，更新 package.json，自动更新依赖</li> <li>gem-mine update 支持 --with_public 选项，此时会自动更新 public 目录</li> </ul> </li> <li><p>gem-mine-template</p> <ul> <li>npm start 端口冲突时，自动选择可用端口</li> </ul> </li> <li>gem-mine-docs<ul> <li>首页样式优化</li> </ul> </li> </ul> </li> <li><p><code>2018年01月16日</code></p> <ul> <li>gem-mine 0.1.8，update 命令只更新 webpack 相关内容，不处理其他源码</li> <li>gem-mine-template<ul> <li>修正样式无法热更新问题</li> <li>npm start 默认启用热更新</li> <li>对于现代浏览器，webpack 使用 3x 版本</li> <li>修正一些 IE 兼容性 的 polyfill</li> </ul> </li> <li>文档系统发布</li> </ul> </li> </ul> ";

