	module.exports = "<h1 id=->待办事项</h1> <ul> <li><span class=gray>[不处理，sourcemap 无问题，devtools 语法高亮问题是 chrome 带来的，create-react-app 也是如此] jsx 文件的 sourcemap 有问题，并且在 devtools 中没有语法高亮</span></li> <li><span class=green>[已处理] 提供 style lint</span></li> <li><span class=green>[已处理] postcss 配置检查并完善</span></li> <li><p>提供单元测试能力</p> </li> <li><p><span class=green>[已处理] 提高打包速度，调研以下方案：</span></p> <ul> <li><span class=green>[已处理] dev 中也使用 dll plugin 方案</span></li> <li><span class=gray>[暂不加入，测试后对目前打包方案没有速度提升] happypack 方案</span></li> <li><span class=green>[已处理] webpack cache</span></li> <li><span class=green>[已处理] babel cache</span></li> <li><span class=gray>[暂不加入，测试后对目前打包方案没有速度提升] hard-source-webpack-plugin</span></li> <li><span class=gray>[暂不加入，测试后对目前打包方案没有速度提升] webpack-uglify-parallel</span></li> <li><span class=gray>[暂不加入，测试后对目前打包方案没有速度提升] parallel-webpack</span></li> </ul> </li> <li><p>多页面打包能力</p> </li> <li><p>国际化能力</p> <ul> <li>业务国际化</li> <li>UI 组件库国际化管理</li> <li>工具类国际化定制（moment）</li> <li>支持远程翻译资源文件（工具类）</li> </ul> </li> <li><span class=green>[已处理] webpack 根据项目是否支持 IE8 进行自动选择版本，现代浏览器可以使用 webpack 3 or 4。目前脚手架中为了兼容 IE8，采用了 webpack 1.x</span></li> <li>资源自动化 CDN 能力<ul> <li>支持资源前缀配置</li> <li>阿里云（工具类）</li> <li>CS（内网，工具类）</li> </ul> </li> </ul> ";

